* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none !important;
}
/* BottomNavigation */
.bottom-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: white;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.1);
  z-index: 90;
}
.bottom-menu .bottom-menu-wrapper {
  display: flex;
  height: 100%;
}
.bottom-menu .bottom-menu-wrapper .item {
  flex: 1;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  display: flex;
  color: #666666;
}
.bottom-menu .bottom-menu-wrapper .item .icon {
  width: 25px;
  height: 25px;
}
.bottom-menu .bottom-menu-wrapper .item .title {
  font-size: 10px;
}
/* Toolbar */
.toolbar {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 9;
  position: fixed;
  background-color: #fff;
}
.toolbar img {
  height: 20px;
}
.toolbar .navigation-button {
  display: flex;
  align-items: center;
  padding-left: 10px;
}
.toolbar .action-menu {
  display: flex;
  align-items: center;
  padding-right: 10px;
}
.toolbar .action-add {
  padding-right: 10px;
  padding-top: 5px;
}
/* DrawerLayout */
.drawer-layout {
  width: 280px;
  height: 100%;
  z-index: 100;
  top: 0;
  position: fixed;
  background-color: #fbfbfb;
}
/* Mask */
.mask {
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.6;
  position: fixed;
  z-index: 99;
}
/* ViewPager */
.viewpager {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background-color: #fbfbfb;
  padding-top: 60px;
}
/* Today */

/* NewHabit */
.new-habit {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #f5f7f8;
}
/* Tag */
.tag {
  margin-left: 8px;
}
.tag-item {
  color: rgba(0, 0, 0, 0.65);
  display: inline-block;
  font-size: 16px;
  line-height: 30px;
  padding: 0 16px;
  border-radius: 16px;
  margin-right: 8px;
  border: 1px solid #d9d9d9;
  margin-top: 60px;
}
.tag-active {
  background-color: #ffd330;
}
/* TargetCard */
.card {
  padding-top: 20px;
}
.card-item {
  width: 105px;
  height: 140px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-left: 8px;
  position: relative;
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
}
.card-info {
  position: absolute;
  top: 12px;
  left: 12px;
}
.card-name {
  font-size: 16px;
}
.card-count {
  padding-top: 4px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.45);
}
.card-icon {
  position: absolute;
  bottom: 8px;
  right: 14px;
}
.card-icon img {
  width: 50px;
  height: 50px;
}
.tabs {
  overflow: hidden;
  position: relative;
}
.tabs-content {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.tabs-content-animated {
  transition: margin-left 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.tabpane {
  width: 100%;
  flex-shrink: 0;
}
/* UserCenter */
.user-center {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #f5f7f8;
}
/* UserHeader */
.user-header {
  background: #fff;
  height: 100px;
  padding-top: 50px;
  padding-left: 24px;
  display: flex;
  align-items: center;
}
.avatar {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  color: #fff;
  background: #ccc;
  font-size: 28px;
}
.user-info {
  padding-left: 12px;
}
.user-info-more {
  position: absolute;
  right: 24px;
  transform: rotate(180deg);
}
.banner {
  height: 80px;
  background: #fff;
  margin-top: 10px;
  margin-bottom: 10px;
}
.cate-all {
  display: flex;
  justify-content: space-around;
  background: #fff;
  padding-top: 15px;
  padding-bottom: 15px;
}
.cate-item img {
  width: 50px;
  height: 50px;
}
.cate-item p {
  font-size: 14px;
  color: #333;
  margin-top: 6px;
}
.user-cnter-menu {
  margin-top: 10px;
}
.menu-item {
  display: flex;
  height: 50px;
  background: #fff;
  align-items: center;
  justify-content: space-between;
}
.menu-icon {
  width: 30px;
  height: 30px;
  margin-left: 10px;
}
.icon-enter {
  height: 20px;
  margin-right: 10px;
}
/* EditTarget */
.edit-target-header {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.add-target-bg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.target-icon {
  width: 30px;
  height: 30px;
}
.edit-target-input {
  width: 100%;
  height: 32px;
  background-color: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}
